import { Component } from 'react';
import ViewModel from '../viewmodel/viewmodel';
// import { AnimationFrameScheduler } from 'rxjs/internal/scheduler/AnimationFrameScheduler';
/*
* autor:张建新  zjx@yonyou.com   
* date: 2018/07/17
* ClientMonitor for App,render count etc.
* useage: 
*/
const _monitorDataPath = 'monitro.ui.data';
class ClientMonitor extends Component {
	constructor(props) {
		super(props);
		this.ViewModel = ViewModel;
		this._page = null;
		this.pageName = '';
		this.firstStartTime = null;
		this.flag_Monitor = 'sys_flag_bmonitor';
	}

	reset = () => {
		this.getMonitorData().count = 0;
		this.getMonitorData().countNumber = [];
	};
	getMonitorData = () => {
		if(this._page&&this._page.refs&&this._page.refs.monitorpage){
				return this._page.refs.monitorpage.props.ViewModel.getData(this.getCurrentNamespace(this.pageName));
		}else{
			return {WARINING:'监控异常，请刷新页面重试'};
		}
	};
	getbMonitor = () => {
		let v = 'false';
		try{
			v = sessionStorage.getItem(this.flag_Monitor);
			if (v == null) {
				v = 'false';
			}
		}catch(ex){
			v = 'false';
		}
		return eval(v);
	};
	endMonitor = () => {
		//
		if(!this.getbMonitor()){
			
			return {WARINING:'监控没有开启，请开启 window.sys_monitor.startMonitor()'};
		}
		//
		sessionStorage.setItem(this.flag_Monitor, false);
		let monitorData = this.getMonitorData();
		if(typeof(monitorData.count) == "undefined"){
			
			return {WARINING:'当前操作，没有触发页面重绘'};
		}
		
		// let cost =(monitorData.endRenderTime.getTime()-monitorData.startRenderTime.getTime())/1000;
		monitorData.summary = '组件' + this.pageName + ':共渲染' + monitorData.count + '次';
		monitorData.others = { 其它: '此为扩展项' };

		//
		console.log(monitorData.summary);
		monitorData.countNumber.forEach(function(v, k) {
			console.log(v);
		});
		let result = JSON.stringify(monitorData);
		console.log(result);
		this.reset();
		return result;
	};
	getCurrentNamespace = (name) => {
		return _monitorDataPath + '.' + name;
	};
	startMonitor = () => {
		sessionStorage.setItem(this.flag_Monitor, true);
		return true;
	};
	initMonitor = (page) => {
		this._page = page;
		let mp = page.refs.monitorpage;
		this.pageName = mp.constructor.name;
		//
		let d = this.getMonitorData();
		if(d&&d._rC){return;}
		//
		let monitorData = {};
		monitorData.countNumber = [];
		monitorData.count = 0;
		//
		let rC = mp.render;
		mp.render = this.renderC;
		monitorData._rC = rC;
		//
		let cduC = mp.componentDidUpdate;
		mp.componentDidUpdate = this.componentDidUpdateC;
		monitorData._cudC = cduC;
		//
		mp.props.ViewModel.setData(this.getCurrentNamespace(this.pageName), monitorData);
	};

	componentDidUpdateC = function() {
		console.log('---monitor:' + this.constructor.name + '组件更新完毕---');
		let currenNamespace = _monitorDataPath + '.' + this.constructor.name;
		let monitorData = this.props.ViewModel.getData(currenNamespace);
		//
		if(monitorData._cudC){
			monitorData._cudC.bind(this)();
		}
		let endRenderTime = new Date();

		let cost = (endRenderTime.getTime() - monitorData.startRenderTime.getTime()) / 1000;
		monitorData.countNumber[monitorData.count] = '第' + monitorData.count + '次渲染耗时：' + cost + '秒';
		monitorData.count++;
		monitorData.endRenderTime = endRenderTime;
	};
	renderC = function() {
		let currenNamespace = _monitorDataPath + '.' + this.constructor.name;
		let monitorData = this.props.ViewModel.getData(currenNamespace);
		monitorData.startRenderTime = new Date();
		console.log('---monitor:组件' + this.constructor.name + '开始render---');
		if(monitorData._rC&&monitorData._rC.bind){
			return monitorData._rC.bind(this)();
		}
	};
}
window.sys_monitor= new ClientMonitor();
if(window.parent){
	// 这里出现一个问题  在ie下  子窗口对象会替换父窗口对象  如果子窗口关闭  会导致window丢失 然后window.eval方法报未定义  -- bbqin
	// 修改方案为  父窗口不存在再添加
	!window.parent.sys_monitor && (window.parent.sys_monitor = window.sys_monitor);
}
export default ClientMonitor;
