import React, { Component } from 'react';
import DatePicker from 'bee-datepicker';
import moment from 'moment';
import { getDateFunNode, checkFuntionValue, getTimeFunctionLabel } from './dateFunction';
import NCRangePicker from '../nc_RangePicker';
import { locale } from '../../public/timer';
import { localeLang } from '../../public/currentLocale';
import { HotKeys } from 'react-hotkeys';
import 'moment/locale/zh-cn';
import 'moment/locale/zh-tw';
import 'moment/locale/en-au';
import PubSub from 'pubsub-js';


switch (localeLang) {
    case 'simpchn':
        moment.locale('zh-cn');
        break;
    case 'english':
        moment.locale('en');
        break;
    case 'tradchn':
        moment.locale('zh-tw');
        break;
    default:
        moment.locale('zh-cn');
}

require('./index.less');
require('bee-datepicker/build/DatePicker.css');

class NCDatePicker extends Component {
	constructor(props) {
		super(props);
		this.state = {
			// open: this.props.datepickerOpen || false,
			// add by bbqin 添加value控制
			value: this.props.value,
			hasCloseBtn:false
		};
	}
	componentWillMount(){
		let { defaultValue , value} = this.props


		if(defaultValue || value){
			this.setState({
				hasCloseBtn:true
			})
		}

	}
	componentWillReceiveProps(nextProps) {
		if(nextProps.value && !this.state.hasCloseBtn){
			this.setState({
				hasCloseBtn:true
			})
		}
		this.setState({
			// 取上一次的狀態  by bbqin
			// open: nextProps.datepickerOpen === undefined ? this.state.open : nextProps.datepickerOpen,
			value: nextProps.value
		});
	}

	componentDidMount() {
		// 做下自動聚焦
		let { tableOpen, autoFocus } = this.props;
		if (autoFocus && this.dateInputWrap && tableOpen) {
			let elem = this.dateInputWrap.querySelector('input'); // 获取到底层的input
			elem && elem.focus();
		}
	}

	componentDidUpdate() {
		// 做下自動聚焦
		let { tableOpen, autoFocus } = this.props;
		if (autoFocus && this.dateInputWrap && tableOpen) {
			let elem = this.dateInputWrap.querySelector('input'); // 获取到底层的input
			elem && elem.focus();
		}
	}

	onClick = (e) => {
		let props = this.props;
		// debugger;
		props.onClick && props.onClick(e, this.dateInput);
		// this.setState({
		// 	open: this.props.datepickerOpen == undefined ? true : this.state.open
		// });
		// 添加一个状态来区分是否时鼠标和tab操作  by bbqin
		this.clicked = true;
	};

	// 添加 onOpenChange  来控制关闭时重新聚焦  --bbqin
	onOpenChange = (isOpen, value, formatValue) => {
		let props = this.props;
		// 目前自动聚焦还有些问题  主要是点击其他表单元素时应该聚焦为其他元素
		if (isOpen === false && !this.clicked && this.dateInputWrap) {
			let elem = this.dateInputWrap.querySelector('input'); // 获取到底层的input
			elem.focus();
		}
		// 关闭之后认为  clicked为false  加这个属性时为了不影响原来的鼠标鼠标操作
		if (isOpen === false) {
			this.clicked = false;
			// 改变一下状态  避免状态被记录
			// this.setState({
			// 	open: false
			// });
		}
		// if (isOpen === true) {
		//     // 改变一下状态  避免状态被记录
		//     this.setState({
		//         open: true
		//     });
		// }
		// 这里需要调用一下外部的编辑前事件   目前的问题是抛出的对象和click中不同
		if (isOpen === true && !this.clicked) {
			props.onClick && props.onClick(this.dateInput, this.dateInput);
		}

		props.onOpenChange && props.onOpenChange(isOpen, value, formatValue);

		// 发布一个清除事件 用于快捷键关闭 by bbqin
		if (isOpen) {
			PubSub.subscribe('clearOpenPanel', (msg, { data }) => {
				if (this.dateInputWrap) {
					let elem = this.dateInputWrap.querySelector('input'); // 获取到底层的input
					console.log('clearOpenPanel subscribe ');
					elem && elem.click();
				}
				// this.dateInput.setState({open: false})
            });
		} else {
			PubSub.unsubscribe('clearOpenPanel');
		}
	};

	//添加x按钮，清空输入框的日期
	clearValue = () => {
		let { disabled } = this.props;
		this.setState({
			hasCloseBtn:false,
			value:''
		})
		if(disabled !== true){//编辑态的时候点击才清空显示的日期
			this.onChange(null);
		}
	}
	onChange=(val,MultiTZ,format) => {
		if (MultiTZ) {
			this.props.onChange && this.props.onChange(val);
		} else {
			this.props.onChange && this.props.onChange(!val ? val : val.format(format));
		}
	}
	render() {
		let {
			format = 'YYYY-MM-DD',
			value,
			showStar,
			onChange,
			MultiTZ = false,
			disabledDate,
			showTimeFunction,
			placeholder,
			autoFocus,
			disabled,
			className,
			...others
		} = this.props;
		let { hasCloseBtn } = this.state;
		// 使用状态上的value值来记录  by bbqin
		value = this.state.value;
		let functionValue = value || '';
		if (checkFuntionValue(value)) {
			value = '';
			placeholder = '';
		}
		return (
			<HotKeys
				className="hotkeys-datepicker-wrapper"
				keyMap={{
					datepickerClear: 'del'
				}}
				handlers={{
					datepickerClear: () => {
						// 调用内部的状态进行清空  by bbqin
						this.setState(
							{
								value: ''
							},
							() => {
								// 调用外部change事件进行清空
								if (MultiTZ) {
									this.props.onChange && this.props.onChange('');
								} else {
									this.props.onChange && this.props.onChange('');
								}
							}
						);
					}
				}}
			>
				<div
					className={`u-form-control-wrapper ${className}`}
					onClick={(e) => {
						e.stopPropagation();
					}}
					ref={(dom) => {
						this.dateInputWrap = dom;
					}}
					onKeyDown={(e) => {
						// tab键
						let isTab = [ e.keyCode, e.which, e.charCode ].find((e) => e === 9);
						// console.log(
						//     "datepicker onblur trigger",
						//     isTab,
						//     e,
						//     this.props.onBlur
						// );
						// 判断是否键下tab键和shift 键  bbqin
						if ((isTab && e.shiftKey) || isTab) {
							setTimeout(() => {
								// 实际情况下发现 不添加timeout shift+tab 无法跳转
								// 猜测 可能是 原生shift+tab 事件与当前事件有了执行顺序
								this.props.onBlur && this.props.onBlur(value);
							}, 0);
						}
					}}
				>
					{showStar ? <span className="datepicker-input-required-tag">*</span> : ''}
					<div style={{ position: 'relative' }} className="nc-datepicker-input-wrap">
						<DatePicker
							animation=''
							// 加一个唯一class
							className={`nc-input nc_DatePicker ${className}`}
							format={format}
							locale={locale}
							value={value ? moment(value) : null}
							showToday={false}
							placeholder={placeholder}
                            disabled = {disabled}
							disabledDate={(current, next) => {
								this.props.disabledDate && this.props.disabledDate(current, next, moment);
							}}
							ref={(dateInput) => {
								this.dateInput = dateInput; // 获取到组件对象 -- bbqin
							}}
							renderIcon={this.props.renderIcon}
							onChange={(val)=>{
								this.onChange(val,MultiTZ,format)
							}}
							renderFooter={() => getDateFunNode(functionValue, onChange, showTimeFunction)}
							{...others}
							onClick={this.onClick}
							// open={this.state.open}
							onOpenChange={this.onOpenChange.bind(this)}
							dateInputPlaceholder={format || 'yyyy-MM-dd HH:mm:ss'}
							// onBlur={() => {
							//     console.log("datepicker onblur trigger");
							// }}
							//添加属性,去掉内层输入框---剩余功能：面板下移到录入框下面
							//showDateInput={false}
							//style={{'top':35}}
						/>
						{hasCloseBtn  && disabled !== true && <i
							className="nc-datepicker-del-i iconfont icon-qingkong"
							onClick={() => {
								this.clearValue();
							}}
						/>}
						{getTimeFunctionLabel(functionValue, showTimeFunction)}
					</div>
				</div>
			</HotKeys>
		);
	}
}
NCDatePicker.defaultProps = {
	renderIcon: () => {
		return <i className="icon iconfont">&#xe6cd;</i>;
	}
};
NCDatePicker.NCRangePicker = NCRangePicker;
export default NCDatePicker;
