import React, { Component } from 'react';
import DatePicker from 'bee-datepicker';
require('bee-datepicker/build/DatePicker.css');
import { isArray, isObj, deepClone } from '../../public';
import moment from 'moment';
import { getDateFunNode, checkFuntionValue, getTimeFunctionLabel } from './dateFunction';
import { locale } from '../../public/timer';
import { localeLang } from '../../public/currentLocale';
import 'moment/locale/zh-cn';
import 'moment/locale/zh-tw';
import 'moment/locale/en-au';
if (localeLang === 'simpchn') {
    moment.locale('zh-cn');
} else if (localeLang === 'english') {
    moment.locale('en');
} else if (localeLang === 'tradchn') {
    moment.locale('zh-tw');
} else {
    moment.locale(`${localeLang}`);
}

const { RangePicker } = DatePicker;

require('./index.less');

class NCRangePicker extends Component {
	constructor(props) {
		super(props);
		this.state = {
			localeLang: 'simpchn'
		};
	}
	componentWillMount() {
		this.setState({ localeLang });
	}
	render() {
		let {
			format = 'YYYY-MM-DD',
			showStar,
			defaultValue,
			value,
			onChange,
			showTimeFunction,
			placeholder,
			tableOpen,
			...others
		} = this.props;

		let functionValue = value || [];
		if (checkFuntionValue(value)) {
			value = '';
			placeholder = ' ';
		}
		if (tableOpen) {
			return (
				<div
					className={`u-form-control-wrapper ${this.props.className}`}
					onClick={(e) => {
						e.stopPropagation();
					}}
				>
					{showStar ? <span className="range-picker-required-tag">*</span> : ''}
					<RangePicker
						className="nc-input"
						format={format}
						locale={locale}
						placeholder={placeholder}
						renderIcon={this.props.renderIcon}
						defaultValue={value ? value.map((e) => moment(e)) : []}
						onChange={(v) => {
							this.props.onChange && this.props.onChange(v.map((e) => e.format(format)));
						}}
						value={value ? value.map((e) => moment(e)) : []}
						{...others}
					/>
					{getTimeFunctionLabel(functionValue, showTimeFunction)}
				</div>
			);
		} else {
			return (
				<div
					className={`u-form-control-wrapper ${this.props.className}`}
					onClick={(e) => {
						e.stopPropagation();
					}}
				>
					{showStar ? <span className="range-picker-required-tag">*</span> : ''}
					<RangePicker
						className="nc-input"
						format={format}
						locale={locale}
						placeholder={placeholder}
						renderIcon={this.props.renderIcon}
						defaultValue={value ? value.map((e) => moment(e)) : []}
						onChange={(v) => {
							this.props.onChange && this.props.onChange(v.map((e) => e.format(format)));
						}}
						value={value ? value.map((e) => moment(e)) : []}
						{...others}
					/>
					{getTimeFunctionLabel(functionValue, showTimeFunction)}
				</div>
			);
		}
	}
}

NCRangePicker.defaultProps = {
	renderIcon: () => {
		return <i className="icon iconfont">&#xe6cd;</i>;
	}
};
export default NCRangePicker;
